<template>
	<view class="container">
		<scroll-view scroll-y="true">

			<view class="title"><text>确认退款信息</text></view>

			<uni-forms ref="form" :model="aftersaleFormData" @submit="" class="prod-list" validate-trigger="blur">
				<uni-group v-for="(prod, index) in aftersaleFormData.items" :key="index">
					<view class="item">
						<view class="prod-info">
							<view class="pic">
								<image :src="orderItems[index].pic"></image>
							</view>
							<view class="opt">
								<view class="prod-name">{{ orderItems[index].prodName }}</view>
								<text
									:class="'prod-info-text ' + (orderItems[index].skuName ? '' : 'empty-n')">{{ prod.skuName }}</text>

								<view class="properties">
									<text class="color">颜色:{{ orderItems[index].color }}</text>
									<text class="size">尺码:{{ orderItems[index].size }}</text>
								</view>
								<view class="price-count">
									<text class="count-symbol">数量</text>
									<text class="count-num">{{ orderItems[index].prodCount }}</text>
									<text class="price-symbol">¥</text>
									<text class="price-num">{{ orderItems[index].price }}</text>
								</view>
							</view>
						</view>
					</view>
					<uni-forms-item class="refund-item" @click="toReasonPage" :data-index="index" label="退款原因"
						:name="['items',index,'reason']">
						<uni-easyinput disabled :inputBorder="false" :styles="{color: 'grey'}" suffixIcon="forward"
							class="refund-input" v-model="aftersaleFormData.items[index].reason">
						</uni-easyinput>
					</uni-forms-item>
					<!-- <uni-forms-item class="refund-item" label="退款金额" :name="['items',index,'refundAmount']" :rules="[
						{
							format:'number',
							errorMessage: '价格必须是数字类型',
						},
						{
						maximum: orderItems[index].price,
						errorMessage: '价格超过最大值',
					},{
						required: true,
						errorMessage: '退款金额必填',
					}, {
						errorMessage: '请输入有效的价格',
						pattern: /^(0{1}(\.\d{1,2})?|[1-9]\d*(\.\d{1,2})?)$/,
					}]"> -->
						<view class="flex-container">
							<view>¥</view>
							<uni-easyinput :inputBorder="false" :styles="{color: 'grey'}" class="refund-input-left"
								v-model="aftersaleFormData.items[index].refundAmount" :clearable="false">
							</uni-easyinput>
							<view>(协商后修改,最多¥{{orderItems[index].price}})</view>
						</view>

					</uni-forms-item>
					<uni-forms-item class="refund-item" @click="toDescribePage" :data-index="index" label="上传描述和凭证">
						<uni-easyinput disabled :inputBorder="false" :styles="{color: 'grey'}" suffixIcon="forward"
							class="refund-input"
							:value="aftersaleFormData.items[index].imgExplain === '' && aftersaleFormData.items[index].textExplain == '' ? '上传有助处理问题':'已上传'">
						</uni-easyinput>
					</uni-forms-item>
				</uni-group>
			</uni-forms>
			<button type="primary" @click="submitOrder" class="next-step">提交申请</button>
		</scroll-view>

	</view>
</template>
<script>
	var http = require("../../utils/http.js");

	export default {
		data() {
			return {
				aftersaleType: 0,
				aftersaleData: [],
				aftersaleFormData: {
					items: []
				},
				//存放已选中的值
				orderItems: [{
						orderNumber: "ffd12323",
						orderItemId: 2323,
						prodId: 1,
						pic: "https://help.apple.com/assets/640A52196275DE31D4371B5E/640A52216275DE31D4371B6F/zh_CN/f7ffef42e5ee59debb22975fd137f12d.png",
						prodName: "你爸爸最喜欢的iphone",
						prodCount: 2,
						color: "红色",
						size: "xl",
						price: 20.90,
						checked: false
					},
					{
						orderNumber: "ffd12323",
						orderItemId: 23223,
						prodId: 2,
						pic: "https://help.apple.com/assets/640A52196275DE31D4371B5E/640A52216275DE31D4371B6F/zh_CN/f7ffef42e5ee59debb22975fd137f12d.png",
						prodName: "你爸爸最喜欢的iphone",
						prodCount: 2,
						color: "红色",
						size: "xl",
						price: 20.90,
						checked: false
					}
				],
				reasonList: [{
					reason: "不想要了"
				}, {
					reason: "你是sb"
				}],
				reasonCheckIndex: [0, 1],


			};
		},

		onLoad: function(options) {
			this.setData({
				// orderItems: JSON.parse(uni.getStorageSync("aftersaleOrderItems")),
				//reasonList: JSON.parse(uni.getStorageSync("aftersaleReasonList")),
				//reasonCheckIndex: options.reasonCheckIndex,
				//aftersaleType: options:aftersaleType,
			});


			for (var i = 0; i < this.orderItems.length; i++) {
				var orderItem = this.orderItems[i];
				//TODO img_explain
				var aftersaleFormDataItem = {
					refundAmount: orderItem.price,
					reason: this.reasonList[this.reasonCheckIndex[i]].reason,
				}
				this.aftersaleFormData.items.push(aftersaleFormDataItem);

				var aftersaleDataItem = {
					orderNumber: orderItem.orderNumber,
					orderItemId: orderItem.orderItemId,
					aftersaleType: this.aftersaleType,
					imgExplain: '',
					textExplain: '',
				}
				this.aftersaleData.push(aftersaleDataItem);
			}

			uni.$on('aftersaleReasonChange', function(reasonChange) {
				this.reasonCheckIndex[reasonChange.listIndex] = reasonChange.checkIndex;
				this.aftersaleFormData.items[reasonChange.listIndex].reason = this.reasonList[reasonChange
					.checkIndex];
			})

		},

		onUnload: function() {
			uni.$off('aftersaleReasonChange');
		},

		methods: {
			toReasonPage: function(e) {
				var index = e.currentTarget.dataset.index;

				uni.navigateTo({
					url: "/pages/aftersale-apply/aftersale-reason?listIndex=" + index + "&checkIndex=" + this
						.reasonCheckIndex[index],
				})
			},

			//TODO
			toDescribePage: function() {
				uni.navigateTo({
					url: "/pages/aftersale-apply/aftersale-describe",
				})
			},

			submitOrder: function() {
				// 使用 Promise
				// 对整个表单进行校验，返回一个 Promise
				this.$refs.form.validate().then((res) => {
					// 成功返回，res 为表单数据
					// 其他逻辑处理 
					// ...
					uni.showLoading();
					var formItems = res.items;
					for (var i = 0; i < this.aftersaleData.length; i++) {
						Object.assign(this.aftersaleData[i], formItems[i]);
					}
					var params = {
						url: "/p/aftersale/order/createOrder",
						method: "POST",
						data: {
							aftersaleOrderDtoList: this.aftersaleData,
						},
						callBack: (res) => {
							uni.hideLoading();
							//TODO 成功后跳转售后列表页

						},
						errCallBack: (res) => {
							uni.hideLoading();
							uni.showToast({
								title: res.msg,
								icon: "none",
							})
						},
					};
					http.request(params);

				}).catch((err) => {
					// 表单校验验失败，err 为具体错误信息
					// 其他逻辑处理
					// ...
					console.log(err);
					uni.showToast({
						title: err.message,
						icon: "none",
					})
				})

			},

		},
	};
</script>

<style lang="less">
	page {
		background-color: #f4f4f4;
	}

	.input-with-text-align-right {
		text-align: right;
	}


	.container {
		height: 100vh;
		padding-bottom: 100rpx;

		scroll-view {
			height: 100%;
		}

		.title {
			font-weight: 800;
			text-align: center;
			margin-top: 40rpx;
			margin-bottom: 40rpx;
		}

		.prod-list {
			font-size: 28rpx;

			.item {
				padding: 20rpx;
				background: #fff;
				display: flex;
				align-items: center;

				.prod-info {
					display: flex;

					.pic {
						text-align: center;
						width: 150rpx;
						height: 150rpx;

						image {
							max-width: 100%;
							max-height: 100%;
							border-radius: 8rpx;
							vertical-align: middle;
						}

					}

					.opt {
						line-height: 180%;
						margin-left: 30rpx;

						.properties {
							color: grey;

							.size {
								margin-left: 30rpx;
							}
						}

						.price-count {
							.price-symbol {
								margin-left: 30rpx;

							}
						}
					}
				}
			}

			.refund-item {
				padding-left: 20rpx;
				white-space: nowrap;

				/* 防止文字换行 */
				.flex-container {
					display: flex;
					align-items: center;
					padding-right: 10rpx;

					.refund-input-left {
						padding-left: 0;
						text-align: left;

					}
				}

				.refund-input {
					text-align: right;

				}
			}



			.refund-field {
				padding: 30rpx;
				background: #fff;
				align-items: center;

				.refund-price {
					margin-left: 20rpx;
					display: flex;
					align-items: center;

					.refund-price-label {
						margin-right: 10rpx;
					}
				}

				.refund-content {

					color: gray;
					margin-left: auto;
					display: flex;
					align-items: center;

					.price-input {
						width: 130rpx;

					}
				}
			}
		}

		.next-step {
			position: absolute;
			bottom: 0;
			width: 100%;
		}
	}
</style>